.button-closed {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: none;
}

.information-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-column: span 3;
  border-left: 2px solid $black;
  border-right: 2px solid $black;
  border-bottom: 2px solid $black;
  height: 100%;
}

.information-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 2px solid $black;
  width: 100%;
  height: 100%;

  &:last-child {
    border-bottom: none;
  }
}

.information-list__item-text {
  @include fontface-regular;
  font-size: 32px;
  line-height: 24.8px;
  padding: 0;
  margin: 0 0 10px;

  @media (max-width: $tablet-width) {
    font-size: 20px;
  }
}

.information-phone {
  @include fontface-regular;
  font-size: 36px;
  line-height: 43.57px;
  color: $black;

  @media (max-width: $tablet-width) {
    font-size: 25px;
  }

  &:hover {
    background-color: $black;
    color: $white;
  }
}

.contacts__heading {
  @include fontface-regular;
  font-size: 32px;
  line-height: 38.73px;
  padding: 0;
  margin: 0 0 10px;

  @media (max-width: $tablet-width) {
    font-size: 25px;
  }
}

.contacts__address {
  @include fontface-regular;
  display: flex;
  flex-direction: column;
  gap: 15px;
  font-size: 30px;
  line-height: 30px;
  padding: 0 0 10px;
  margin: 0;

  @media (max-width: $tablet-width) {
    font-size: 20px;
  }
}

.item-burger__show {
  display: none;
}

.page-nav-text {
  @include fontface-regular;
  display: none;
  font-size: 35.96px;
  line-height: 53.2px;
  text-transform: uppercase;
  height: 100%;
  justify-content: center;
  flex-direction: column;
  border-top: 2px solid $black;
  border-right: 2px solid $black;

  span {
    font-size: 47.95px;
  }
}

.page-header__nav.active {
  width: 100%;
  height: 100vh;
  background-color: $white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  color: $black;
  padding: 20px;
  transition: 0.4s all ease;

  .button-closed {
    display: inline-block;
    position: absolute;
    right: 25px;
    top: 30px;
    z-index: 1;
  }

  .item-burger__show {
    display: flex;
  }

  .page-header__bar {
    background-image: url("../img/Logo-nav-burger.png");
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: 70%;
    height: 100%;
    border-top: 2px solid $black;
    border-left: 2px solid $black;
    border-right: 2px solid $black;
  }

  .page-header__logo {
    display: none;
  }

  .page-nav-text {
    display: flex;
    grid-column: span 2;
    margin: 0;
    padding: 0;
    padding-left: 50px;

    @media (max-width: $tablet900-width) {
      font-size: 25px;
      line-height: 30px;

      span {
        font-size: 30px;
      }
    }

    @media (max-width: $tablet-width) {
      padding-left: 30px;
      font-size: 18px;
      line-height: 24px;

      span {
        font-size: 30px;
        line-height: 35px;
      }
    }

    @media (max-width: $mobile480-width) {
      padding-left: 10px;
      font-size: 16px;

      span {
        font-size: 23px;
        line-height: 25px;
      }
    }
  }

  .navigation-list {
    grid-area: 2 / 1 / 5 / -1;
    height: 100%;
    z-index: 3;
  }

  .burger {
    display: none;
  }

  .navigation-list__mobile {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    border: 2px solid $black;

    @media (max-width: $mobile480-width) {
      grid-template-columns: 1fr 1fr;
    }
  }

  .navigation-list__item {
    height: 100%;
    cursor: pointer;

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(7),
    &:nth-child(8) {
      border-right: 2px solid $black;

      @media (max-width: $mobile480-width) {
        border: none;
      }
    }

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6) {
      border-bottom: 2px solid $black;
    }

    @media (max-width: $mobile480-width) {
      &:nth-child(odd) {
        border-right: 2px solid $black;
      }
      border-bottom: 2px solid $black;
    }

    &:hover {
      background-color: $black;

      .navigation-list__item-link {
        color: $white;
      }
    }
  }

  .navigation-list__item-link {
    @include fontface-regular;
    color: $black;
    font-size: 32px;
    line-height: 24.8px;
    padding: 25px 0;
    display: flex;
    width: 100%;
    justify-content: center;

    @media (max-width: $tablet900-width) {
      font-size: 25px;
    }

    @media (max-width: $tablet-width) {
      font-size: 16px;
      padding: 10px 0;
    }
  }

  .user-list {
    justify-content: center;
    grid-area: 4 / 3 / 5 / -1;
    width: 100%;
    border-right: 2px solid $black;
    position: relative;
    display: flex;
    align-items: center;

    @media (max-width: $mobile480-width) {
      grid-area: 5 / 1 / 7 / -1;
      border-left: 2px solid $black;
      border-bottom: 2px solid $black;
    }
  }

  .user-reg {
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $black;
    padding: 0;
    cursor: pointer;

    &:hover {
      background-color: $black;

      .user-reg__link {
        color: $white;
      }
    }
  }

  .user-reg__link {
    color: $black;
    font-size: 32px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    border: none;
    height: 60px;
    width: 200px;
    text-align: center;

    &:hover {
      background-color: transparent;
    }

    @media (max-width: $tablet-width) {
      font-size: 16px;
      width: 150px;
    }
  }

  .user-log {
    display: none;
  }

  .user-log__link {
    padding: 0;
  }

  .user-log__mobile {
    display: block;
    padding: 0;

    svg {
      width: 50px;
      height: 50px;
    }

    path {
      stroke: $black;
    }
  }

  .social-list {
    grid-column: span 3;
    border-bottom: 2px solid $black;
    border-left: 2px solid $black;
    border-right: 2px solid $black;
    gap: 30px;
    padding: 0;

    @media (max-width: $desktop-width) {
      display: flex;
    }
  }

  .social-list__link {
    border: 1px solid $black;
  }

  .telegram-link {
    path {
      fill: $black;
    }

    &:hover {
      background-color: $black;

      path {
        fill: $white;
      }
    }
  }

  .whatsapp-link {
    &:hover {
      rect {
        fill: $white;
      }

      path {
        fill: $black;
      }
    }
  }

  .navigation-children-list {
    width: 100%;

    @media (max-width: $mobile480-width) {
      bottom: -70px;
    }
  }

  .navigation-children-list-item__link,
  .navigation-list__item-link {
    @media (max-width: $tablet-width) {
      font-size: 14px;
    }

    @media (max-width: $mobile480-width) {
      padding: 10px;
      font-size: 13px;
      text-align: left;
    }
  }

  .current-item {
    color: $white;
    background-color: $black;
  }

  .navigation-children-list-item__link {
    &.current-item {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $white;
      background-color: $light-blue;
    }
  }

  .user-list__item.user-log__mobile {
    &:hover {
      background-color: $black;

      svg {
        fill: $white;
      }
    }
  }

  .user-log__link.current-item {
    svg {
      fill: $white;
    }
  }
}
